.char {
  offset-path: path(
    "M.5 122.7s24.7-275 276.9 0c327.1 356.7 266.1-330.3 548-33.3 256.9 270.7 271.1 0 271.1 0"
  );
  offset-distance: calc(var(--char-index) * 1.75rem);
  position: absolute;
  -webkit-animation: wave 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate
      calc(var(--char-index) * 20ms),
    wave2 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate
      calc(var(--char-index) * 20ms);
          animation: wave 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate
      calc(var(--char-index) * 20ms),
    wave2 2200ms cubic-bezier(0.62, 0.01, 0.42, 1.01) infinite alternate
      calc(var(--char-index) * 20ms);
  transform: perspective(800px) scale(1) rotate3d(0, 0, 0, 0);
}

@-webkit-keyframes wave {
  100% {
    offset-distance: calc((var(--char-index) * 3rem) + 300px);
    transform: perspective(800px) scale(1.25) rotate3d(1, 0, 0, 180deg);
    color: red;
  }
}

@keyframes wave {
  100% {
    offset-distance: calc((var(--char-index) * 3rem) + 300px);
    transform: perspective(800px) scale(1.25) rotate3d(1, 0, 0, 180deg);
    color: red;
  }
}

@-webkit-keyframes wave2 {
  100% {
    offset-distance: calc((var(--char-index) * 3.45rem) + 600px);
    transform: perspective(800px) scale(1.75) rotate3d(1, 0, 0, 360deg);
    color: goldenrod;
  }
}

@keyframes wave2 {
  100% {
    offset-distance: calc((var(--char-index) * 3.45rem) + 600px);
    transform: perspective(800px) scale(1.75) rotate3d(1, 0, 0, 360deg);
    color: goldenrod;
  }
}
