.content {
  max-height: 100%;
}

html {
  height: 100%;
}
body {
  height: 100%;
}
#root {
  height: 100%;
}
.App {
  height: 100%;
}

.page-enter {
  opacity: 0;
  transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

.wrapper:hover .text {
  display: block;
}

.text,
.wrapper:hover .icon {
  display: none;
}
.icon,
.text {
  color: white;
}

.text {
  font-size: 13px;
}

.side-bar {
  list-style: none;
  margin-top: 2rem;
  margin-right: 3rem;
}

.list_style {
  margin-top: 2rem;
  text-align: center;
  max-width: 1.5rem;
  max-height: 1.5rem;
}
li.list_style > a {
  text-decoration: none;
}

.background_colour {
  display: flex;
  height: 100%;
  flex-direction: column;
  background-color: #383838;
  height: 100vh;
}

